// validacionCompra.js
document.addEventListener('DOMContentLoaded', function() {
    const titularInput = document.getElementById('titular');
    const numerosTarjetaInput = document.getElementById('numeros-tarjeta');
    const fechaTarjetaInput = document.getElementById('fecha-tarjeta');
    const cvvTarjetaInput = document.getElementById('cvv-tarjeta');
    const comprarBtn = document.getElementById('comprarBtn');

    const validateFields = () => {
        const titularValue = titularInput.value.trim();
        const numerosTarjetaValue = numerosTarjetaInput.value.trim();
        const fechaTarjetaValue = fechaTarjetaInput.value.trim();
        const cvvTarjetaValue = cvvTarjetaInput.value.trim();

        const isValidTitular = /^[a-zA-Z\s]+$/.test(titularValue) && titularValue.length === 20;
        const isValidNumerosTarjeta = /^\d+$/.test(numerosTarjetaValue) && numerosTarjetaValue.length === 16;
        const isValidFechaTarjeta = /^\d+$/.test(fechaTarjetaValue) && fechaTarjetaValue.length === 4;
        const isValidCVVTarjeta = /^\d+$/.test(cvvTarjetaValue) && cvvTarjetaValue.length === 3;

        displayErrorMessage(isValidTitular, 'error-titular');
        displayErrorMessage(isValidNumerosTarjeta, 'error-numeros-tarjeta');
        displayErrorMessage(isValidFechaTarjeta, 'error-fecha-tarjeta');
        displayErrorMessage(isValidCVVTarjeta, 'error-cvv-tarjeta');

        if (isValidTitular && isValidNumerosTarjeta && isValidFechaTarjeta && isValidCVVTarjeta) {
            comprarBtn.disabled = false;
            comprarBtn.style.opacity = 1;
        } else {
            comprarBtn.disabled = true;
            comprarBtn.style.opacity = 0.5;
        }
    };

    const displayErrorMessage = (isValid, elementId) => {
        const errorMessage = document.getElementById(elementId);
        errorMessage.style.display = isValid ? 'none' : 'block';
    };

    titularInput.addEventListener('input', validateFields);
    numerosTarjetaInput.addEventListener('input', validateFields);
    fechaTarjetaInput.addEventListener('input', validateFields);
    cvvTarjetaInput.addEventListener('input', validateFields);

    // Validación inicial al cargar la página
    validateFields();
});